.container {
    max-width: 1440px;
    margin: 0 auto;
    padding: 0px 15px;
    font-family: 'Rubik', sans-serif;
    font-size: 14px;
}

@media (max-width: 1440px) {
    .container {
        max-width: 1200px;
    }
}

@media (max-width: 1200px) {
    .container {
        max-width: 970px;
    }
}


@media (max-width: 992px) {
    .container {
        max-width: 750px;
    }
}

@media (max-width: 767px) {
    .container {
        max-width: none;
    }
}


/*container*/


.title {
    font-weight: 600;
    font-size: 30px;
    line-height: 45px;
    position: relative;
    z-index: 1;
    margin-bottom: 12px;
}




.searchBlock {
    display: grid;
    grid-template-columns: 9fr 1fr;
    gap: 52px;
    align-items: center;
}


@media (max-width: 450px) {
    .searchBlock {
        display: grid;
        grid-template-columns: 1fr;
        gap: 10px;
    }
}

.categoryBlock {
    margin-top: 50px;
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 40px;
    margin-bottom: 50px;
}

.category p {
    font-size: 20px;
    font-weight: 500;
    margin-bottom: 20px;
}

.category div {
    display: flex;
    gap: 30px;
}

@media (max-width: 991px) {
    .categoryBlock {
        grid-template-columns: 1fr;
        gap: 10px;
    }

    .vacancyCards {
        grid-template-columns: 1fr;
    }
}


@media (max-width: 550px) {
    .bacalavr {
        display: flex !important;
        flex-wrap: wrap;
    }
}


@media (max-width: 1200px) {
    .bacalavr {
        display: flex !important;
        flex-wrap: wrap;
        gap: 10px !important;
    }

    .master {
        display: flex !important;
        flex-wrap: wrap;
        gap: 10px !important;
    }
}

.inputWrapper {
    display: flex;
}

.search {
    width: 100%;
    margin-bottom: 34px;
}

.searchInput {
    padding: 12px 15px;
    font-weight: 300;
    font-size: 14px;
    font-family: inherit;
    outline: none;
    border: 1px solid #858585;
    border-radius: 10px 0 0 10px;
    border-right: none;
    flex-basis: 90%;
}

.searchButton {
    background-color: white;
    color: #121212;
    padding: 12px 32px;
    font-size: 16px;
    font-weight: 500;
    font-family: inherit;
    border-radius: 0 10px 10px 0;
    outline: none;
    border: 1px solid #858585;
    cursor: pointer;
    transition: 0.3s;
    flex-basis: 10%;
}

.searchButton:hover {
    background-color: rgba(0, 0, 0, 0.05);
}

/* // */

.searchTitle {
    font-weight: 500;
    font-size: 20px;
    margin-bottom: 12px;
}